@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

body {
  background-color: #f1f1f1;
  counter-reset: #0f141e;

  font: normal 16px  'Roboto', sans-serif;
}

.avatar { 
  width: 240px;
  border-radius: 14px;
  box-shadow: 4px 4px 14px rgba(0, 0, 0, 0.25);
}

.info { 
  margin-bottom: 20px;
}


.container { 
  max-width: 750px;
  margin: auto;
  padding-top: 3px;
}

a { 
  color: black;
  text-decoration: none;
}

a:hover { 
  text-decoration: underline
}

a:active { 
  color: blue;
}

a:visited { 
  color: grey;
}

.card {
  padding: 15px 20px;
  margin-bottom: 20px;
  border-radius: 20px;
} 

.br-yellow {  
  background: #fff9dc;
}

.bg-green { 
  background: #e5ffe7;
}

.bg-red { 
  background: #ffe5e5 ;
}

.card h2 { 
  margin-bottom: 10px;
}

.list {
  padding: 0 20px;
  list-style: none;
}

.list li { 
  color: #86898f;
  margin-bottom: 10px;
}

.table { 
  width: 100%;
}

.table td { 
  padding-bottom: 10px;
}

.table td:nth-last-child(2),
.table td:nth-last-child(2) { 
  color: #86898f;
}

.link-list {
  list-style: none;
  padding: 0;
}

.link-list li {
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 1px solid #ccc;
}

.contact h2 { 
  text-align: center;
}   

.form-group label { 
  display: block;
  font-size: 14px;
  color: #86898f;
}

.form-group input,
.form-group textarea { 
  width: 100%;
  box-sizing: border-box;
  border: none;
  background: transparent;
  border-bottom: 1px solid #ccc;
  color: #000;
  padding: 15px 0 10px;
  outline: none;
  font-family: inherit;
}

.row { 
  display: flex;
  margin-bottom: 20px;
}

.row > .form-group { 
  width: 50%;
  margin-right: 20px;
}

.row > .form-group:last-child { 
  margin-right: 0;
}

.info { 
  display: flex;
}

@media (max-width: 700px) { 
  .info { 
    text-align: center;
    display: block;
  }
}

.info-text {
  margin-left: 40px;
}

.info-text h2 { 
  margin-bottom: 10px;
}

.btn { 
  background: #111;
  padding: 15px 20px;
  color: #fff;
  border: none;
  font-family: inherit;
  cursor: pointer;
}

.btn:hover {
  opacity: 0.9;
}

/* position */

.position { 
  position: relative;
  border: 4px solid black;
  margin-top: 40px;
  padding: 20px;
  height: 300px;
}

.box { 
  position: fixed;
  width: 50px;
  height: 50px;
  background: blueviolet;
  bottom: 0;
  right: 0;
  bottom: 50px;
  right: 50px;
  border-radius: 50%;
}